<template>
  <div class="hello">
    <div class="bysking" id="chartDom"></div>
    <hr>
    <div class="bottom-btn">
      <el-button @click="refresh" type="primary">刷新数据</el-button>
      <el-button @click="exportPic" type="primary">预览截图</el-button>
      <el-button type="primary">想下载？试一试点击右上角下载图标</el-button>
    </div>
    <div class="pre-view">
      <el-button v-if="curImg" @click="curImg = ''" type="primary">删除截图</el-button>
      <img style="height: 200px;" :src="curImg" alt="" srcset="">
    </div>
  </div>
</template>

