let xAxisData = [
  "休闲服务",
  "食品",
  "家用",
  "汽车",
  "交通运输",
  "银行",
  "纺织服务",
  "医药生物",
  "计算机",
  "化工",
];
let data1 = [];
let data2 = [];

for (let i = 0; i < 10; i++) {
  data1.push((Math.random() * 2).toFixed(2));
  data2.push((Math.random() * 5).toFixed(2));
}

let emphasisStyle = {
  itemStyle: {
    shadowBlur: 10,
    shadowColor: "rgba(0,0,0,0.3)",
  },
};
const baseOption = {
  legend: {
    data: ["MIN", "MAX"],
    orient: "vertical",
    right: "10%",
  },
  toolbox: {
    feature: {
      saveAsImage: {},
    },
  },
  tooltip: {},
  xAxis: {
    data: xAxisData,
    name: "行业",
    axisLine: { onZero: true },
    splitLine: { show: false },
    splitArea: { show: false },
  },
  yAxis: {
    show: true,
    // axisLine: { onZero: true },
    name: "市盈率（倍）",
  },
  grid: {
    bottom: 100,
  },
  series: [
    {
      name: "MIN",
      type: "bar",
      stack: "one",
      emphasis: emphasisStyle,
      data: data1,
      label: {
        // show: true,
        position: "inside",
      },
    },
    {
      name: "MAX",
      type: "bar",
      stack: "one",
      emphasis: emphasisStyle,
      data: data2,
      label: {
        show: true,
        position: "top",
      },
    },
  ],
};
export default {
  name: "HelloWorld",

  data() {
    return {
      myChart: null,
      curImg: ''
    };
  },
  mounted() {
    this.initChart();

    this.$nextTick(() => {
      this.loadData();
    });
  },

  methods: {
    initChart() {
      let dom = document.getElementById("chartDom");
      this.myChart = echarts.init(dom);
    },

    loadData() {
      let opt = cloneDeep(baseOption);
      opt = this.handleSetData(opt);
      this.myChart.setOption(opt);
    },

    handleSetData(opt) {
      let data1 = [];
      let data2 = [];

      for (let i = 0; i < 10; i++) {
        data1.push((Math.random() * 2).toFixed(2));
        data2.push((Math.random() * 5).toFixed(2));
      }

      opt.series[0].data = data1;
      opt.series[1].data = data2;
      return opt;
    },

    refresh() {
      this.loadData();
    },

    exportPic() {
      let src = this.myChart.getDataURL({
        pixelRatio: 2,
        backgroundColor: "#fff",
      });

      this.curImg = src;

      // downloadFileByBase64(src, '图片') //调用
    },

